import {CurrencyPipe} from '@angular/common';
import {ContentChild, Directive, ElementRef, HostBinding, HostListener} from '@angular/core';
import {TextInput} from 'ionic-angular';

// import {Events} from 'ionic-angular';



/**
 * Generated class for the CurrencyDirective directive.
 *
 * See https://angular.io/api/core/Directive for more info on Angular
 * Directives.
 */
@Directive({
  selector: '[pcc-currency]',  // Attribute selector
  providers: [CurrencyPipe],
})
export class CurrencyDirective {
  @ContentChild(TextInput) ion_input: TextInput;
  orig: string;
  valor: string;
  i: boolean = false;

  @HostListener('keyup', ['$event'])
  onkeyup(event: KeyboardEvent) {
    if (!this.i) {
      this.orig = this.ion_input.value.toString();
      console.log(this.orig);
      this.i = true;
    } else if (event.keyCode >= 96 && event.keyCode <= 105) {
      // this.orig = this.value;
      this.orig = this.unformat(this.ion_input.value.toString());
      console.log(this.orig);
    } else if (event.keyCode == 8) {
      this.orig = this.unformat(this.ion_input.value.toString())
      console.log(this.orig);
    }

    // this.orig = this.orig.substring(0, this.orig.length - 2) + ',' +
    //  this.orig.substring(this.orig.length - 2, this.orig.length);
    this.valor = this.orig.substring(0, this.orig.length - 2) + ',' +
        this.orig.substring(this.orig.length - 2, this.orig.length);
    console.log(this.valor);

    this.valor = this.cur.transform(this.orig, 'BRL', true, '1.2-2');
    console.log(this.valor);

    // this.ion_input.setValue(this.valor);
  }



  constructor(
      private ele: ElementRef,
      private cur: CurrencyPipe,
  ) {}



  unformat(valor: string) {
    valor = valor.replace('.', '');
    valor = valor.replace(',', '');
    valor = valor.replace('R$', '');
    return valor;
  }
}



